import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    makeStyles,
    CardMedia,
    Card
} from '@material-ui/core';
import { begData } from '../../info/provincia';
import ContentPaper from './contentPaper';
import { isMobile } from 'react-device-detect';
import provStyles from '../../styles/provStyle';

const BilleteraContent = () => {
    const [select, setSelect] = useState('');
    const classes = provStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={8}>
                    <Grid container spacing={isMobile ? 2 : 3}>
                        <Grid item xs={12} sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textmed}>
                                    {begData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={6}>
                            <Paper elevation={3} className={classes.cardMini}>
                                <Typography className={classes.textmed}>
                                    {begData.card1.txtB}
                                </Typography>
                            </Paper>
                        </Grid>

                        <Grid item xs={6} sm={6}>
                            <Paper elevation={3} className={classes.cardMini}>
                                <Typography className={classes.textmed}>
                                    {begData.card1.txtC}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Paper elevation={3} className={classes.cardVert}>
                        <Typography className={classes.textBig}>
                            {begData.card2.txtA}
                        </Typography>
                        <Typography className={classes.textmed}>
                            {begData.card2.txtB}
                        </Typography>
                    </Paper>
                    <Card
                        elevation={select !== 'solicitar' ? 3 : 5}
                        onMouseEnter={() => setSelect('solicitar')}
                        onMouseLeave={() => setSelect('')}
                        className={classes.media}
                        onClick={() => window.open(begData.card4.url)}
                    >
                        <CardMedia
                            image={begData.card4.img}
                            style={{ height: '100%' }}
                        />

                        <Typography className={classes.textmed}>
                            {begData.card0.txtB}
                        </Typography>
                    </Card>

                    <Card
                        elevation={select !== 'app' ? 3 : 5}
                        className={classes.media}
                        style={{ padding: 3 }}
                        onMouseEnter={() => setSelect('app')}
                        onMouseLeave={() => setSelect('')}
                        onClick={() => window.open(begData.card5.url)}
                    >
                        <CardMedia
                            image={begData.card5.img}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

export default BilleteraContent;
