import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Fab, Grid, Grow, IconButton, makeStyles } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import noi from '../assets/noima.jpg';
import colores from '../styles/colors';
import jorA from '../assets/jor/jorConcejo.jpg';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import BusinessIcon from '@material-ui/icons/Business';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';

const ProyectosModal = ({ open, handleClose }) => {
    const classes = useStyles();

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar style={{ backgroundColor: colores.mainLightfull }}>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Proyectos Presentados en HCSF
                        </Typography>
                    </Toolbar>
                </AppBar>
                <div className={classes.contEjes}>
                    <ProyectContent />
                </div>
            </Dialog>
        </div>
    );
};

const ProyectContent = () => {
    const classes = useStyles();
    return (
        <div>
            <Grid
                container
                spacing={2}
                justify="center"
                style={{
                    paddingRight: isMobile ? 0 : 40,
                    paddingLeft: isMobile ? 0 : 40
                }}
            >
                {!isMobile && (
                    <Grid item sm={6}>
                        {/*   <Card
                            style={{
                                borderRadius: 15,
                                width: '100%',
                                height: '200'
                            }}
                        >
                            <CardMedia
                                image={jorA}
                                style={{
                                    borderRadius: 15,
                                    width: '100%',
                                    height: '200'
                                }}
                                title="fefe"
                            />
                        </Card> */}
                        <div style={{ borderRadius: 15 }}>
                            <Grow
                                direction="up"
                                in={true}
                                mountOnEnter
                                timeout={600}
                            >
                                <img
                                    src={jorA}
                                    alt="jorr"
                                    style={{
                                        width: '100%',
                                        height: null,
                                        borderRadius: 15
                                    }}
                                />
                            </Grow>
                        </div>
                    </Grid>
                )}

                <Grid item xs={12} sm={6}>
                    <Grid container spacing={2}>
                        {dataDash.map((dataCard, i) => (
                            <Grow
                                direction="up"
                                in={true}
                                mountOnEnter
                                timeout={400 * i + 600}
                            >
                                <Grid item xs={12} sm={6}>
                                    <CardDash {...dataCard} />
                                </Grid>
                            </Grow>
                        ))}
                    </Grid>
                </Grid>

                {proyectos.map((proyect, i) => {
                    //<Grow direction="up" in={true} mountOnEnter  timeout={600 * i + 600}></Grow>
                    return (
                        <Grid item sm={6}>
                            <CardProyect {...proyect} />
                        </Grid>
                    );
                })}
            </Grid>
        </div>
    );
};

const CardDash = ({ title, total, icon, color, texColor, icoColor }) => {
    if (total === 0) {
        return (
            <Card
                elevation={3}
                style={{
                    height: '100%',
                    backgroundColor: color,
                    display: 'flex',
                    flexDirection: 'column-reverse'
                }}
            >
                <CardContent
                    style={{
                        textAlign: 'center',
                        alignContent: 'space-between'
                    }}
                >
                    <Typography
                        style={{
                            fontSize: isMobile ? 20 : 22,
                            color: texColor
                        }}
                    >
                        {title}
                    </Typography>
                    <Fab
                        style={{
                            height: isMobile ? 50 : 60,
                            width: isMobile ? 50 : 60,
                            pointerEvents: 'none',
                            marginTop: 30,
                            backgroundColor: icoColor
                        }}
                    >
                        {icon}
                    </Fab>
                </CardContent>
            </Card>
        );
    }

    return (
        <Card elevation={3} style={{ backgroundColor: color }}>
            <CardContent style={{ textAlign: 'center' }}>
                <Fab
                    style={{
                        height: 45,
                        width: 45,
                        marginBottom: 10,
                        pointerEvents: 'none',
                        backgroundColor: icoColor
                    }}
                >
                    {' '}
                    {icon}
                </Fab>

                <Typography
                    style={{ fontSize: isMobile ? 40 : 40, color: texColor }}
                >
                    +{total}
                </Typography>
                <Typography
                    style={{ fontSize: isMobile ? 18 : 22, color: texColor }}
                >
                    {title}
                </Typography>
            </CardContent>
        </Card>
    );
};

const CardProyect = ({ name, text, img }) => {
    const [shadow, setShadow] = useState(2);
    return (
        <Card
            elevation={shadow}
            style={{ height: 250, display: 'flex', borderRadius: 10 }}
            onMouseEnter={() => setShadow(5)}
            onMouseLeave={() => setShadow(1)}
        >
            <CardMedia image={img} title={name} style={{ width: '55%' }} />

            <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                    {name}
                </Typography>
                <Typography gutterBottom variant="subtitle2" component="p">
                    {text}
                </Typography>
            </CardContent>
        </Card>
    );
};

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

