import { useState } from 'react';
import {
    IconButton,
    makeStyles,
    Typography,
    withStyles,
    Avatar
} from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import ModalContent from '../pages/ModalContent';
import LocationCityIcon from '@material-ui/icons/LocationCity';
import ApartmentIcon from '@material-ui/icons/Apartment';
import EjesModal from './ejesModal';
import DashboardIcon from '@material-ui/icons/Dashboard';
import LibraryAddCheckIcon from '@material-ui/icons/LibraryAddCheck';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import SimpleMap from './map';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import colores from '../styles/colors';
import Fab from '@material-ui/core/Fab';
import Paper from '@material-ui/core/Paper';
import ProyectosModal from './proyectModal';
import MyModal from './myModal';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import { Slide } from '@material-ui/core';

const NavigationBar = () => {
    const classes = useStyles();
    const [value, setValue] = useState(null);
    const [ejesHandler, setEjesHandler] = useState(false);
    const [proyectHandler, setProyectHandler] = useState(false);
    const [myHandler, setMyHandler] = useState(false);
    const [modalHandler, setModalHandler] = useState(false);
    const [modalData, setModalData] = useState({});

    const mainButtons = [
        {
            id: 'prov',
            icon: <BeenhereIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Accion con Provincia'
        },
        {
            id: 'ejes',
            icon: <DashboardIcon style={{ color: colores.darkBlueNav }} />,
            name: `Ejes de Trabajo`
        },
        {
            id: 'proyectos',
            icon: <BookmarksIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Proyectos en el Concejo'
        },
        {
            id: 'ciudad',
            icon: <ApartmentIcon style={{ color: colores.darkBlueNav }} />,
            name: 'La Ciudad que Queremos'
        }
    ];

    function handlerModal(data) {
        setModalData(data);
        setModalHandler(true);
    }

    return (
        <>
            <div className={classes.bottomNav}>
                <BottomNavigation
                    value={value}
                    onChange={(event, newValue) => {
                        setValue(newValue);
                    }}
                    className={classes.paperMain}
                >
                    {mainButtons.map((btn, i) => (
                        <BottomNavigationAction
                            key={i}
                            icon={
                                <Slide
                                    direction="up"
                                    in={true}
                                    mountOnEnter
                                    timeout={400 * i + 900}
                                >
                                    <Fab aria-label={btn.name} key={btn.name}>
                                        {btn.icon}
                                    </Fab>
                                </Slide>
                            }
                            showLabel
                            onClick={() => handlerModal(btn)}
                            label={
                                <Paper className={classes.tipBtn}>
                                    <Typography className={classes.textBtn}>
                                        {' '}
                                        {btn.name}
                                    </Typography>
                                </Paper>
                            }
                            value={i}
                        />
                    ))}
                </BottomNavigation>
            </div>

            <EjesModal
                open={ejesHandler}
                handleClose={() => {
                    setEjesHandler(false);
                    setValue(null);
                }}
            />

            <ProyectosModal
                open={proyectHandler}
                handleClose={() => {
                    setProyectHandler(false);
                    setValue(null);
                }}
            />

            <MyModal
                open={myHandler}
                handleClose={() => {
                    setMyHandler(false);
                    setValue(null);
                }}
            />
            <ModalContent
                data={modalData}
                tabi={modalData.id}
                open={modalHandler}
                handleClose={() => {
                    setModalHandler(false);
                    setValue(null);
                }}
            />
        </>
    );
};

const useStyles = makeStyles(() => ({
    circularBtn: {
        borderWidth: 2,
        borderColor: 'blue'
    },
    paperMain: {
        marginRight: 'auto',
        marginLeft: 'auto',
        height: 60,
        backgroundColor: colores.gamBdark,
        width: 640,
        borderRadius: 10
    },
    bottomNav: {
        position: 'absolute',
        bottom: 60,
        alignSelf: 'center'
    },
    tipBtn: {
        marginTop: 5,
        padding: 8,
        backgroundColor: colores.mainLight2
    },
    textBtn: {
        fontSize: 14,
        letterSpacing: 1.4,
        color: colores.gamBdarkfull
    }
}));

