import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import { FacebookProvider, EmbeddedPost } from 'react-facebook';
import AmbienteContent from './ejesContent/ambiente';
import DeportesContent from './ejesContent/deportes';
import GeneroContent from './ejesContent/genero';
import BarriosContent from './ejesContent/barrios';
import EjesContent from '../custom/ejesContent';
import Grow from '@material-ui/core/Grow';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import colores from '../styles/colors';
import ejesData from '../info/ejes';

const EjesModal = ({ open, handleClose }) => {
    const [swCont, setSwCont] = useState('ambiente');
    const classes = useStyles();
    const [dataEje, setDataEje] = useState(ejesData[0]);

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar style={{ backgroundColor: colores.mainLightfull }}>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Ejes De Trabajo
                        </Typography>
                    </Toolbar>
                </AppBar>
                <div
                    style={{
                        paddingLeft: isMobile ? 12 : 90,
                        paddingRight: isMobile ? 12 : 90,
                        paddingTop: 20,
                        paddingBottom: 20,
                        backgroundColor: colores.fondoLight
                    }}
                >
                    <ContentEjes
                        setSwCont={setSwCont}
                        swCont={swCont}
                        setDataEje={setDataEje}
                    />
                    <Divider variant="middle" style={{ marginTop: 15 }} />
                    <div>
                        <Typography
                            style={{
                                fontSize: 24,
                                letterSpacing: 2.6,
                                textAlign: 'center',
                                color: dataEje.letra
                            }}
                        >
                            {dataEje.name}
                        </Typography>
                    </div>
                    <Divider variant="middle" style={{ marginBottom: 15 }} />
                    <EjesContent data={dataEje} />

                    {/* {!isMobile &&  <ContentFacebook /> } */}
                </div>
            </Dialog>
        </div>
    );
};

const ContentFacebook = () => {
    const FbPost = [
        'https://www.facebook.com/jorgelinamudallel/posts/4102542703165503',
        'https://www.facebook.com/jorgelinamudallel/posts/4157726427647130',
        'https://www.facebook.com/jorgelinamudallel/posts/4195750497178056'
        //	'https://www.facebook.com/jorgelinamudallel/posts/4177032202383219'
    ];

    return (
        <FacebookProvider appId="865953314130137">
            <Grid container spacion={3} style={{ alignContent: 'center' }}>
                {FbPost.map((post) => (
                    <Grid item sm={4}>
                        <EmbeddedPost
                            width={isMobile ? 200 : 250}
                            href={post}
                        />
                    </Grid>
                ))}
            </Grid>
        </FacebookProvider>
    );
};

const ContentEjes = ({ setSwCont, swCont, setDataEje }) => {
    return (
        <Grid
            container
            spacing={isMobile ? 1 : 4}
            style={{
                width: isMobile ? '100%' : '90%',
                marginRight: 'auto',
                marginLeft: 'auto'
            }}
        >
            {ejesData.map((eje, i) => (
                <Grow in={true} mountOnEnter timeout={400 * i + 400}>
                    <Grid item xs={3} sm={3}>
                        <CardEjes
                            eje={eje}
                            setSwCont={setSwCont}
                            swCont={swCont}
                            setDataEje={setDataEje}
                        />
                    </Grid>
                </Grow>
            ))}
        </Grid>
    );
};

const CardEjes = ({ eje, setSwCont, swCont, setDataEje }) => {
    return (
        <Card
            elevation={swCont === eje.id ? 1 : 5}
            style={{
                //width: isMobile && 80,
                padding: isMobile ? 2 : 0,
                marginLeft: eje.id === 'ambiente' && -4,
                borderRadius: isMobile ? '15%' : 15,
                textAlign: 'center',
                color: swCont !== eje.id && eje.color,
                backgroundColor:
                    swCont === eje.id ? eje.color : colores.mainLight
            }}
            onClick={() => {
                setSwCont(eje.id);
                setDataEje(eje);
            }}
        >
            <CardActionArea>
                {isMobile ? (
                    eje.ico
                ) : (
                    <CardMedia
                        style={{
                            height: 80,
                            marginTop: 5,
                            marginRight: 5,
                            marginLeft: 5,
                            borderRadius: 15
                        }}
                        image={eje.img}
                        title={eje.title}
                    />
                )}
                <Typography
                    gutterBottom
                    variant="h5"
                    style={{
                        fontSize: isMobile ? 12 : 18,
                        marginTop: 5,
                        color: eje.letra
                    }}
                >
                    {eje.name}
                </Typography>
            </CardActionArea>
        </Card>
    );
};

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
    appBar: {
        position: 'sticky'
    },

    title: {
        marginLeft: 5,
        color: colores.mainDarkfull
    }
}));

export default EjesModal;
