import React, { forwardRef } from 'react';
import { isMobile } from 'react-device-detect';
import { IconButton, makeStyles, Avatar, Box } from '@material-ui/core';
import PropTypes from 'prop-types';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import colores from '../styles/colors';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import VerticalTabs from './verticalTabs';
import portadaJor from '../assets/portyt.png';
import TabsProv from './tabsProv';
import Beg from './provContent/beg2';
import Billetera from './provContent/billetera';
import Vacunas from './provContent/vacunas';
import Incluir from './provContent/incluir';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-force-tabpanel-${index}`}
            aria-labelledby={`scrollable-force-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={0}>
                    <Typography style={{ textAlign: 'center' }}>
                        {children}
                    </Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

const ProvModal = ({ open, handleClose }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar
                        style={{
                            backgroundColor: colores.mainLightfull,
                            flexDirection: 'row'
                        }}
                    >
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Accion Con Provincia:
                        </Typography>
                    </Toolbar>

                    {isMobile && (
                        <TabsProv
                            provBtn={provBtn}
                            value={value}
                            setValue={setValue}
                        />
                    )}
                </AppBar>

                <div className={classes.provContent}>
                    <BannerProv />
                    {!isMobile && <VerticalTabs />}
                    {isMobile &&
                        provBtn.map((tab, i) => (
                            <TabPanel value={value} index={i}>
                                {tab.tabContent}
                            </TabPanel>
                        ))}
                </div>
            </Dialog>
        </div>
    );
};

const BannerProv = () => {
    return (
        <Card
            elevation={5}
            style={{
                height: isMobile ? 90 : 160,
                marginBottom: 10,
                borderRadius: 15,
                width: isMobile ? '100%' : '60%',
                margin: 'auto'
            }}
        >
            <CardMedia
                image={portadaJor}
                style={{
                    paddingTop: isMobile ? '40%' : '31%', // 16:9,
                    marginTop: '30'
                }}
            />
        </Card>
    );
};

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const provBtn = [
    {
        id: 'BEG',
        tabContent: <Beg />,
        img: begIcon,
        ico: <Avatar src={begIcon} style={{ height: 38, width: 38 }} />
    },
    {
        id: 'Billetera',
        tabContent: <Billetera />,
        img: billetera,
        ico: <Avatar src={billetera} style={{ height: 42, width: 42 }} />
    },
    {
        id: 'Incluir',
        tabContent: <Incluir />,
        img: incluir,
        ico: <Avatar src={incluir} style={{ height: 38, width: 38 }} />
    },
    {
        id: 'Vacunas',
        tabContent: <Vacunas />,
        img: vacuna,
        ico: <Avatar src={vacuna} style={{ height: 38, width: 38 }} />
    }
];

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'sticky'
    },
    provContent: {
        //height: '100%',
        paddingLeft: isMobile ? 10 : 120,
        paddingRight: isMobile ? 10 : 120,
        paddingTop: 20,
        backgroundColor: colores.fondoLight
    },
    title: {
        //marginLeft: 5,
        color: colores.mainDarkfull
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`
    }
}));

export default ProvModal;
