import React from 'react';
import { isMobile, isIOS } from 'react-device-detect';
import PaperText from '../components/paperText';
import {
    Avatar,
    Card,
    CardActionArea,
    CardActions,
    CardMedia,
    Chip,
    IconButton,
    makeStyles,
    Paper
} from '@material-ui/core';
import Container from '../components/container';
import NavigationBar from '../components/navigation';
import SocialButton from '../components/socialButton';
import MessengerCustomerChat from 'react-messenger-customer-chat';
import MobileMenu from '../components/mobileMenu';
import jorL from '../assets/jorLogo.png';
import hacemosL from '../assets/ejesProv/hsf.jpg';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';

export default function Home() {
    const styles = useStyles();

    return (
        <Container>
            <MessengerCustomerChat
                pageId={process.env.REACT_APP_PAGE_FB_ID}
                appId={process.env.REACT_APP_FB_ID}
                htmlRef="https://jorweb.herokuapp.com/"
            />
            <div style={{ position: 'absolute', top: isMobile ? 50 : 0 }}>
                {!isMobile ? <SocialButton /> : <MobileMenu />}
            </div>
            {isMobile && (
                <div
                    style={{
                        position: 'absolute',
                        top: 28,
                        left: 95
                    }}
                >
                    <img src={jorL} style={{ height: 85, marginBottom: 10 }} />
                </div>
            )}

            {!isMobile && (
                <div
                    style={{
                        position: 'absolute',
                        right: 55,
                        top: 55,
                        flexDirection: 'column',
                        display: 'flex'
                    }}
                >
                    <Card
                        style={{
                            height: 140,
                            width: 140,
                            marginBottom: 15,
                            borderRadius: '25%'
                        }}
                    >
                        <CardActionArea
                            onClick={() =>
                                window.open(
                                    'https://mudallel.hacemossantafe.com'
                                )
                            }
                        >
                            <CardMedia
                                style={{
                                    height: 140,
                                    borderRadius: '25%'
                                }}
                                image={hacemosL}
                            />
                        </CardActionArea>
                    </Card>
                    <div
                        style={{
                            flexDirection: 'row',
                            display: 'flex'
                        }}
                    >
                        {directAcc.map((btn, i) => (
                            <Card
                                key={i}
                                style={{
                                    height: 40,
                                    width: 40,
                                    borderRadius: '25%',
                                    margin: 'auto'
                                }}
                                onClick={() => window.open(btn.url)}
                            >
                                <CardActionArea>
                                    <CardMedia
                                        style={{
                                            height: 40
                                        }}
                                        image={btn.img}
                                    />
                                </CardActionArea>
                            </Card>
                        ))}
                    </div>
                </div>
            )}

            <div className={styles.descJor}>
                {!isMobile && (
                    <img
                        src={jorL}
                        style={{
                            height: isMobile ? 85 : 150,
                            marginBottom: 25
                        }}
                    />
                )}

                {isMobile && (
                    <div
                        style={{
                            flexDirection: 'row-reverse',
                            display: 'flex'
                        }}
                    >
                        {directAcc.map((btn, i) => (
                            <Chip
                                key={i}
                                onClick={() => window.open(btn.url)}
                                avatar={<Avatar src={btn.img} />}
                                style={{ margin: 3 }}
                            />
                        ))}
                    </div>
                )}

                <PaperText />
            </div>

            {!isMobile && <NavigationBar />}
        </Container>
    );
}

const directAcc = [
    {
        id: 'BEG',
        img: begIcon,
        url: 'https://sso.santafe.gov.ar/service-auth/login?service=https://www.santafe.gob.ar/boletoeducativo/login'
    },
    {
        id: 'billetera',
        img: billetera,
        url: isIOS
            ? 'https://apps.apple.com/ar/app/billetera-santa-fe/id1558196556'
            : 'https://play.google.com/store/apps/details?id=com.pluspagos.mobile'
    },
    {
        id: 'vacunas',
        img: vacuna,
        url: 'https://www.santafe.gob.ar/santafevacunacovid/inicio'
    }
];

const useStyles = makeStyles((theme) => ({
    typography: {
        padding: theme.spacing(2)
    },

    descJor: {
        position: 'absolute',
        //marginBottom: isMobile ? '15%' : '15%',
        bottom: isMobile ? '10%' : '30%',
        width: isMobile ? '88%' : '36%',
        maxWidth: 480,
        marginLeft: isMobile ? 'auto' : '10%',
        marginRight: isMobile ? 'auto' : null
    }
}));
