import React, { useState } from 'react';

import { Paper, Grid, Typography, makeStyles } from '@material-ui/core';

import { vacunasData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';

const VacunasContent = () => {
    const classes = useStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item sm={8}>
                    <Grid container spacing={3}>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>{vacunasData.text}</Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>
                                    {vacunasData.card2.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card2.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>
                                    {vacunasData.card5.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card5.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={4}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>
                                    {vacunasData.card1.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card1.txtB}
                                </Typography>
                                <Typography>
                                    {vacunasData.card1.txtC}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item sm={4}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>
                                    {vacunasData.card0.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card0.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography>
                                    {vacunasData.card4.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card4.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    );
};

const useStyles = makeStyles(() => ({
    cards: {
        padding: isMobile ? 10 : 20,
        height: 160,
        letterSpacing: 1.6,
        overflow: 'auto',
        borderRadius: 15,
        textAlign: 'center'
    }
}));

export default VacunasContent;
